@media (max-width: 900px) {
	header {
		position: fixed;
		display: flex;
		width: 100%;
		height: 3.2rem;
		background: var(--colorGreen);
		z-index: 999;
	}
	.Footer {
		width: 100%;
		margin-top: 1rem;
		background: var(--colorGreen);
	}
	.nav {
		flex-grow: 1;
	}
	.nav__list {
		display: flex;
		width: 100%;
	}
	.nav__list li {
		margin-left: auto;
		margin-top: 10px;
	}
	.nav__list li:nth-last-of-type(1) {
		margin-right: auto;
	}
	.nav__list li a {
		color: var(--colorWhite);
		font-size: 1.5rem;
	}
	.nav__list li a:hover,
	.nav__list li a:active {
		color: var(--colorBlack);
	}
	.Footer .nav__list li {
		transform: translateY(5px);
	}
}
@media (min-width: 900px) {
	header {
		display: grid;
		place-items: center;
		height: 95vh;
		position: fixed;
		left: 0;
		top: 0;
		bottom: 0;
	}
	.Footer {
		display: grid;
		place-items: center;
		height: 95vh;
		position: fixed;
		right: 0;
		top: 0;
		bottom: 0;
	}
	.nav__list {
		height: 100%;
		display: flex;
		flex-direction: column;
	}
	.nav__list li {
		margin-bottom: 1rem;
		margin-left: 1rem;
	}
	.Footer .nav__list li {
		margin-right: 1rem;
	}
	.nav__list li a {
		color: var(--colorGreen);
		font-size: 1.5rem;
	}
	.nav__list li a:hover,
	.nav__list li a:active {
		color: var(--colorWhite);
	}
}
